import { Helmet } from "react-helmet";
import { Box, Container } from "@mui/material";
import Logo from "./imgs/logo.png";
import { contents } from "./config";
import { LandingPage } from "./components/landingPage/landingPage";
import { Roadmap } from "./components/roadmap/roadmap";
import { About } from "./components/about/about";
import { Kalaidoscope } from "./components/background/kalaidoscope";
import { MenuBar } from "./components/menuBar/menuBar";
import { useRef } from "react";
import { Tokenomics } from "./components/tokenomics/tokenomics";

function App() {
  const roadmapRef = useRef<HTMLDivElement>(null);
  const howToBuyRef = useRef<HTMLDivElement>(null);
  const aboutRef = useRef<HTMLDivElement>(null);
  const tokenomicsRef = useRef<HTMLDivElement>(null);

  return (
    <Box>
      <Helmet>
        <title>{contents.info.title}</title>
        <link rel="icon" type="image/png" href={Logo} sizes="16x16" />
      </Helmet>
      <Kalaidoscope fadeOutRef={aboutRef}  />

      <MenuBar refs={{ howToBuyRef, aboutRef, roadmapRef, tokenomicsRef }} />
      <Container>
        <LandingPage />

        <div ref={aboutRef}>
          <About />
        </div>

        <div ref={tokenomicsRef} style={{ marginTop: "4rem" }}>
          <Tokenomics />
        </div>
        </Container>

        <div ref={roadmapRef} >
          <Roadmap />
        </div>
      
    </Box>
  );
}

export default App;
