import { RefObject, useState } from "react";
import {
  AppBar,
  Box,
  Button,
  Container,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Toolbar,
} from "@mui/material";
import Logo from "../../imgs/logo.png";
import MenuIcon from "@mui/icons-material/Menu";

const GuapLogo = () => {
  return <img src={Logo} style={{ maxWidth: "4rem" }} />;
};

interface MenuBarProps {
  refs: {
    aboutRef: RefObject<HTMLDivElement>;
    roadmapRef: RefObject<HTMLDivElement>;
    howToBuyRef: RefObject<HTMLDivElement>;
    tokenomicsRef: RefObject<HTMLDivElement>;
  };
}

export const MenuBar = ({ refs }: MenuBarProps) => {
  const [open, setOpen] = useState(false);

  const pages = ["About", "Tokenomics", "Roadmap"];

  const toggleDrawer = (newOpen: boolean) => () => {
    setOpen(newOpen);
  };

  const handleCloseNavMenu = (pageName: string) => {
    return () => {
      const refKey = pageToRefKeyMap[pageName];
      const selectedRef = refs[refKey];
      if (selectedRef?.current) {
        selectedRef.current.scrollIntoView({ behavior: "smooth" });
      }
    };
  };

  const pageToRefKeyMap: { [key: string]: keyof MenuBarProps["refs"] } = {
    About: "aboutRef",
    "How to Buy": "howToBuyRef",
    Roadmap: "roadmapRef",
    Tokenomics: "tokenomicsRef",
  };

  return (
    <AppBar
      position="static"
      sx={{
        backdropFilter: "blur(15px)",
        backgroundColor: "rgba(0, 0, 0, 0.7)",
      }}>
      <Container>
        <Toolbar>
          <Box display="flex" justifyContent="space-between" width="100%">
            <IconButton>
              <GuapLogo />
            </IconButton>

            <Box sx={{ display: { xs: "none", md: "flex" } }}>
              {pages.map((page) => (
                <Button
                  key={page}
                  onClick={handleCloseNavMenu(
                    page as keyof MenuBarProps["refs"]
                  )}
                  sx={{
                    my: 2,
                    color: "white",
                    display: "block",
                    fontWeight: "bold",
                    "&:hover": { backgroundColor: "purple" },
                  }}>
                  {page}
                </Button>
              ))}
            </Box>
            <Box sx={{ display: { xs: "flex", md: "none" } }}>
              <IconButton onClick={toggleDrawer(true)}>
                <MenuIcon fontSize="large" sx={{ color: "white" }} />
              </IconButton>
              <Drawer
                anchor="right"
                open={open}
                onClose={toggleDrawer(false)}
                sx={{ backdropFilter: "20px" }}>
                <Box
                  sx={{ width: 250 }}
                  role="presentation"
                  onClick={toggleDrawer(false)}
                  bgcolor={"black"}
                  minHeight="100%">
                  <List>
                    {pages.map((page, index) => (
                      <ListItem key={page} disablePadding>
                        <ListItemButton
                          onClick={handleCloseNavMenu(
                            page as keyof MenuBarProps["refs"]
                          )}>
                          <ListItemText
                            sx={{ color: "white" }}
                            primary={page}
                          />
                        </ListItemButton>
                      </ListItem>
                    ))}
                  </List>
                </Box>
              </Drawer>
            </Box>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
