import { useEffect, useRef } from 'react';
import * as THREE from 'three';

export const Kalaidoscope = ({ fadeOutRef }) => {
    const mountRef = useRef(null);
    const isBlackout = useRef(false);

    useEffect(() => {

        
        // Vertex Shader as a string
        const vertexShader = `
            varying vec2 v_uv;
            void main() {
                v_uv = uv;
                gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
            }
        `;

        // Fragment Shader as a string
        const fragmentShader = `
            uniform vec2 u_resolution;
            uniform float u_time;
            uniform vec2 u_mouse;
            uniform float u_fadeToBlack;
            
            vec3 palette( float t ) {
                vec3 a = vec3(0.5, 0.5, 0.5);
                vec3 b = vec3(0.5, 0.5, 0.5);
                vec3 c = vec3(1.0, 1.0, 1.0);
                vec3 d = vec3(0.263, 0.416, 0.557);
                return a + b * cos( 6.28318 * (c * t + d) );
            }
    
            void main() {        
                vec2 uv = (gl_FragCoord.xy * 2.0 - u_resolution.xy) / u_resolution.y;
                vec2 uv0 = uv;
                vec3 finalColor = vec3(0.0);
                
                for (float i = 0.0; i < 3.0; i++) {
                    uv = fract(uv * 1.5) - 0.5;
                    float d = length(uv) * exp(-length(uv0));
                    vec3 col = palette(length(uv0) + i * 0.4 + u_time * 0.4);
                    d = sin(d * 8.0 + u_time) / 8.0;
                    d = abs(d);
                    d = pow(0.01 / d, 1.2);
                    finalColor += col * d;
                }
                gl_FragColor = vec4(finalColor, 1.0);
                gl_FragColor = mix(vec4(finalColor, 1.0), vec4(0.0, 0.0, 0.0, 1.0), u_fadeToBlack);
            }
        `;

        // Three.js setup
        const scene = new THREE.Scene();
        const camera = new THREE.PerspectiveCamera(
            12,
            window.innerWidth / window.innerHeight,
            0.5,
            10
        );
        camera.position.z = 5;

        const renderer = new THREE.WebGLRenderer();
        renderer.setSize(window.innerWidth, window.innerHeight);
        mountRef.current.appendChild(renderer.domElement);

        const ang_rad = (camera.fov * Math.PI) / 180;
        const fov_y = camera.position.z * Math.tan(ang_rad / 2) * 2;
        const geometry = new THREE.PlaneGeometry(fov_y * camera.aspect, fov_y);

        const uniforms = {
            u_resolution: { value: new THREE.Vector2(window.innerWidth, window.innerHeight) },
            u_time: { value: 0.0 },
            u_mouse: { value: new THREE.Vector2() },
            u_fadeToBlack: { value: 0.0 }
        };

        const material = new THREE.ShaderMaterial({
            vertexShader,
            fragmentShader,
            uniforms
        });

        const plane = new THREE.Mesh(geometry, material);
        scene.add(plane);

        const onWindowResize = () => {
            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();
            renderer.setSize(window.innerWidth, window.innerHeight);
            uniforms.u_resolution.value.set(window.innerWidth, window.innerHeight);
        };

        window.addEventListener('resize', onWindowResize, false);

        const onMouseMove = (event) => {
            uniforms.u_mouse.value.set(event.clientX, event.clientY);
        };

        window.addEventListener('mousemove', onMouseMove, false);

        const clock = new THREE.Clock();

        const checkScroll = () => {
            if (fadeOutRef && fadeOutRef.current) {
                const rect = fadeOutRef.current.getBoundingClientRect();
                
                // Adjust these values to control the start and end of the transition
                const startTransition = -window.innerHeight * 1; // Adjust this to start earlier
                const endTransition = 100; // End when the section is at the top of the viewport
                const scrollPosition = rect.top;
        
                // Normalize scrollPosition between 0 (start transition) and 1 (end transition)
                let progress = (scrollPosition - startTransition) / (endTransition - startTransition);
                progress = Math.min(1, Math.max(0, progress)); // Clamp between 0 and 1
        
                // Apply an easing function for smoother transition
                const easedProgress = progress < 0.5
                    ? 2 * progress * progress
                    : -1 + (4 - 2 * progress) * progress;
        
                uniforms.u_fadeToBlack.value = 1 - easedProgress;
            }
        };
        
        
        

        document.addEventListener('scroll', checkScroll, false);
        checkScroll(); // Initial check

        const animate = () => {
            requestAnimationFrame(animate);
            uniforms.u_time.value = clock.getElapsedTime();
            
            // Fade to black if fadeOutRef is in view
            if (isBlackout.current) {
                // Transition logic here. For example:
                uniforms.u_time.value = 10;
            }

            renderer.render(scene, camera);
        };

        animate();

        return () => {
            document.removeEventListener('scroll', checkScroll);
            window.removeEventListener('resize', onWindowResize);
            window.removeEventListener('mousemove', onMouseMove);
            mountRef.current.removeChild(renderer.domElement);
        };
    }, []);

    return <div ref={mountRef} style={{
        position: 'fixed', // Fixed position
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        zIndex: -1 // Behind the content
    }} />;
};

